import { AddAreaService } from './../../services/AddAreaService';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

import { Location } from '@angular/common';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { Validations } from '../../../validations';
import { UserService } from '../../services/userService';
declare var jQuery: any;



@Component({
  selector: 'add-logistic-manually',
  templateUrl: './add-logistic-manually.component.html',
  styleUrls: [
    './add-logistic-manually.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddAreaService, GetChemistDataService, MeasurementAndDosageSizesService,UserService]
})
export class AddLogisticManuallyComponent implements OnInit {

  public CompanyName: string = "";
  public ContactPersonName: string = "";

  public Address: string = "";
  public Email: string = "";
  public MobileNumber: any;
  public City: any;
  public Area: any;


  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  rowsFilter = [];
  tempFilter = [];
  public runner: boolean = false;

  public Validations: Validations;


  public CompanyNameError = false;
  public ContactPersonNameError = false;
  public MobileNumberError = false;
  public AddressError = false;
  public EmailEmptyError = false;
  public EmailValidError = false;
  public CityError = false;
  public AreaError = false;
  public loaderShow = false;
  public image_error   = false;
  
  public actionBtn = true;

  public ProfilePicture = File;


  public LogoUrl:any;


  @ViewChild(DatatableComponent) logisticList: DatatableComponent;


  constructor(public _AddAreaNameService: AddAreaService,
    public _userService:UserService,
    public _getChemistDataService: GetChemistDataService, public _measurementAndDosageSizesService: MeasurementAndDosageSizesService) {
    this.getAllCities();
    this.Validations = new Validations();

  }


  ngOnInit() {
  }
  onSelect({ selected }) {
    console.log(selected);
  }

  onActivate(event) { }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.LogisticsName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.logisticList.offset = 0;
  }

  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
      });
  }// End of Get All Cities

 
  getCityAreas(elem) {
    this.City = elem;
    this._AddAreaNameService.getAreaName(this.City).subscribe(
      response => {
        this.areas_array = response.data;
      });
  }// End of Get Areas of City

  getAreaSelected(elem) {
    this.Area = elem;
  }

 


  resetErrorFields() {

    this.CompanyNameError = false;
    this.ContactPersonNameError = false;
    this.MobileNumberError = false;
    this.AddressError = false;
    this.EmailEmptyError = false;
    this.EmailValidError = false;
    this.CityError = false;
    this.AreaError = false;
    this.image_error = false;


  }



  
  
  changeImage() {
    $("#imgInp").click();
  }

  changeimage(elem) {
    this.readURL(elem);
  }
  readURL(input) {


    var reader: any,
      target: EventTarget;
    reader = new FileReader();

    input = document.getElementById('imgInp');
    if (input.files && input.files[0]) {
      // var reader = new FileReader();
      var self = this;
      reader.onload = function (e) {
        console.log(e);
        $('#blah').attr('src', e.target.result);
        self.ProfilePicture = input.files[0];
        $("#blah").css("width", "50px");
        self.saveImageService();
      }

      reader.readAsDataURL(input.files[0]);

     
    }
  }


  saveImageService(){
 
      this._userService.saveImageForPharmacy(this.ProfilePicture).subscribe(a=>{
        console.log(a);
        this.LogoUrl = a.data;

      })

  }


  public defaultImage= "../../../../assets/images/pharma-avatar.png";


  emptyForm(){
      this.CompanyName = undefined;
      this.ContactPersonName = undefined;
      this.MobileNumber = undefined;
      this.LogoUrl = undefined;
      this.defaultImage = "../../../../assets/images/pharma-avatar.png"; 
  } 

  addLogistic() {
    /*  var body = {};
      body = {
       CompanyName: this.CompanyName,
       ContactPersonName: this.ContactPersonName,
       MobileNumber: this.MobileNumber,
       Email: this.Email,
       CityId: this.City,
       AreaId: this.Area,
       Address: this.Address,
       UserRole:5
     } */


    console.log(this.Area);

    this.resetErrorFields();

    var error = false;


    if (!this.Validations.verifyNameInputs(this.Email)) {
      error = true;
      this.EmailEmptyError = true;
    } else {
      if (!this.Validations.validateEmail(this.Email)) {
        error = true;
        this.EmailEmptyError = false;
        this.EmailValidError = true;
      }
    }


    if (!this.Validations.verifyNameInputs(this.CompanyName)) {
      error = true;
      this.CompanyNameError = true;
    }

    if (!this.Validations.verifyNameInputs(this.ContactPersonName)) {
      error = true;
      this.ContactPersonNameError = true;
    }

    if(this.LogoUrl == undefined){
      error = true;
      this.image_error = true;
    }


    if (!this.Validations.verifyNameInputs(this.MobileNumber)) {
      error = true;
      this.MobileNumberError = true;
    }

    if (!this.Validations.verifyNameInputs(this.Address)) {
      error = true;
      this.AddressError = true;
    }

    if (this.City == undefined) {
      error = true;
      this.CityError = true;
    }

    if (this.Area == undefined) {
      error = true;
      this.AreaError = true;
    }




    if (error) {

    } else {
      this.loaderShow = true;
      this.actionBtn = false;
      this._measurementAndDosageSizesService.addLogistic(this.CompanyName, this.ContactPersonName
        , this.MobileNumber, this.Email, this.City, this.Area, this.Address,this.LogoUrl, 5).subscribe(Response => {
          console.log(Response);
          if (Response.code == 200) {
            this.loaderShow = false;
            this.actionBtn = true;

            swal(Response.message, "Logistic Created Successfully", "success");

          } else {
            this.loaderShow = false;
            this.actionBtn = true;
            swal(Response.message, "Logistic was not created", "error");

            this.emptyForm();
          }
        }, err => {
          var obj  = JSON.parse(err._body);
          this.loaderShow = false;
          this.actionBtn = true;
          swal(obj.message, "Logistic was not created", "success");
        });

    }




  }

}



