import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AddLogisticManuallyComponent } from './add-logistic-manually.component';
import { AddLogisticManuallyRoutingModule } from './add-logistic-manually-routing.module';

@NgModule({
  imports: [
    CommonModule,
    AddLogisticManuallyRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule,
    HttpModule
  ],
  declarations: [AddLogisticManuallyComponent]
})
export class AddLogisticManuallyModule { }



