import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddLogisticManuallyComponent } from './add-logistic-manually.component';

const routes: Routes = [
  {
    path: '',
    component: AddLogisticManuallyComponent,
    data: {
      title: 'Add Logistic manually',
      status: false
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddLogisticManuallyRoutingModule { }



